import React, { useEffect, useState } from "react";
import { createUserSettingsService } from "../services/user-settings-service";
import { View } from "react-native";
import { ViewLandscape } from "../components/utils/view-landscape";
import {
  Button,
  IconButton,
  ListItem,
  Text,
  TextInput,
} from "@react-native-material/core";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { layoutConstants } from "../themes/layout-contants";
import { setupNotificationsTime, setupNotifications } from "../components/utils/use-notifications";

export function UserSettingsScreen({ navigation }) {
  const userSettingsService = createUserSettingsService();
  const [notificationsTimes, setNotificationsTimes] = useState(null);
  const [notificationsStartTime, setNotificationsStartTime] = useState(null);
  const [notificationsEndTime, setNotificationsEndTime] = useState(null);

  useEffect(() => {
    const syncUserSettings = async () => {
      let settings = await userSettingsService.get();
      settings = JSON.parse(settings);
      setNotificationsTimes(settings.notificationsTimes);
      setNotificationsStartTime(settings.notificationsStartTime);
      setNotificationsEndTime(settings.notificationsEndTime);
    };
    syncUserSettings();
  }, []);

  const increaseTime = (timeString, minutes = 30) => {
    const time = timeString.split(":");
    const date = new Date();
    date.setHours(time[0]);
    date.setMinutes(time[1]);
    return new Date(date.getTime() + minutes * 60000);
  };

  const decreaseTime = (timeString, minutes = 30) => {
    const time = timeString.split(":");
    const date = new Date();
    date.setHours(time[0]);
    date.setMinutes(time[1]);
    return new Date(date.getTime() - minutes * 60000);
  };

  const convert2Decimal = (number) => {
    return ("0" + number).slice(-2);
  };

  const increaseClick = (timeString, setFunc) => {
    const date = increaseTime(timeString);
    setFunc(
      convert2Decimal(date.getHours()) +
        ":" +
        convert2Decimal(date.getMinutes())
    );
  };

  const decreaseClick = (timeString, setFunc) => {
    const date = decreaseTime(timeString);
    setFunc(
      convert2Decimal(date.getHours()) +
        ":" +
        convert2Decimal(date.getMinutes())
    );
  };

  const saveAndBack = async () => {
    const userSettings = {
      notificationsTimes,
      notificationsStartTime,
      notificationsEndTime,
    };
    await userSettingsService.set(userSettings);
    await setupNotificationsTime();
    setupNotifications();
    navigation.goBack();
  };

  return (
    <ViewLandscape>
      <View style={{ flex: 1, backgroundColor: "white" }} className="wrapper">
        <ListItem
          title="Notification"
          leading={<Icon name="bell-alert" size={24} />}
        />
        <View style={{ marginVertical: 10, flex: 1 }}>
          <View
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
            }}
          >
            <Text style={{ flex: 1 }}>Times:</Text>
            <IconButton
              style={{ marginRight: 20 }}
              onPress={() => setNotificationsTimes((parseInt(notificationsTimes) - 1).toString())}
              icon={(props) => <Icon name="minus" {...props} />}
            />
            <TextInput
              variant="outlined"
              value={notificationsTimes}
              onChangeText={setNotificationsTimes}
              style={{
                color: layoutConstants.textColor,
                padding: 10,
                width: 100,
                // flex: 1
              }}
              placeholder="Notification Times"
            />
            <IconButton
              style={{ marginLeft: 20 }}
              onPress={() => setNotificationsTimes((parseInt(notificationsTimes) + 1).toString())}
              icon={(props) => <Icon name="plus" {...props} />}
            />
          </View>
          <View
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
            }}
          >
            <Text style={{ flex: 1 }}>Start Time:</Text>
            <IconButton
              style={{ marginRight: 20 }}
              onPress={() =>
                decreaseClick(notificationsStartTime, setNotificationsStartTime)
              }
              icon={(props) => <Icon name="minus" {...props} />}
            />
            <TextInput
              variant="outlined"
              value={notificationsStartTime || ""}
              onChangeText={setNotificationsStartTime}
              style={{
                color: layoutConstants.textColor,
                padding: 10,
                width: 100,
              }}
              placeholder="Notification Start Time"
            />
            <IconButton
              style={{ marginLeft: 20 }}
              onPress={() =>
                increaseClick(notificationsStartTime, setNotificationsStartTime)
              }
              icon={(props) => <Icon name="plus" {...props} />}
            />
          </View>
          <View
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
            }}
          >
            <Text style={{ flex: 1 }}>End Time:</Text>
            <IconButton
              style={{ marginRight: 20 }}
              onPress={() =>
                decreaseClick(notificationsEndTime, setNotificationsEndTime)
              }
              icon={(props) => <Icon name="minus" {...props} />}
            />
            <TextInput
              variant="outlined"
              value={notificationsEndTime || ""}
              onChangeText={setNotificationsEndTime}
              style={{
                color: layoutConstants.textColor,
                padding: 10,
                width: 100,
              }}
              placeholder="Notification End Time"
            />
            <IconButton
              style={{ marginLeft: 20 }}
              onPress={() =>
                increaseClick(notificationsEndTime, setNotificationsEndTime)
              }
              icon={(props) => <Icon name="plus" {...props} />}
            />
          </View>
        </View>
        <View>
          <Button
            title={"Save"}
            onPress={saveAndBack}
            color={layoutConstants.colorPrimary}
            tintColor={"white"}
            style={{ width: "100%", marginBottom: 20 }}
          />
        </View>
      </View>
    </ViewLandscape>
  );
}
