import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { AppBar, IconButton } from "@react-native-material/core";
import React from "react";
import { layoutConstants } from "../../themes/layout-contants";
import FAB from "./fab";
import { View } from "react-native";

const BottomBar = (props) => {
  const { onMenuClick, onAddClick, hasFAB } = props;
  return (
    <View style={{ position: "absolute", right: 0, bottom: 0, left: 0 }}>
      <AppBar
        variant="bottom"
        color={layoutConstants.colorPrimary}
        enableColorOnDark={true}
        style={{ position: "absolute", start: 0, end: 0, bottom: 0 }}
        leading={(props) => (
          <IconButton
            contentContainerStyle={{ left: 15 }}
            onPress={onMenuClick}
            color={"white"}
            icon={(props) => <Icon name="menu" {...props} color={"white"} />}
            {...props}
          />
        )}
        //   trailing={(props) => (
        //     <IconButton
        //       icon={(props) => <Icon name="magnify" {...props} />}
        //       {...props}
        //     />
        //   )}
      >
        {hasFAB && (
          <FAB
            color={layoutConstants.colorPrimary}
            tintColor={"white"}
            onPress={onAddClick}
            icon={(props) => <Icon name="plus" {...props} />}
            style={{
              position: "absolute",
              top: -28,
              alignSelf: "center",
              borderColor: "white",
              borderWidth: 1,
            }}
          />
        )}
      </AppBar>
    </View>
  );
};

export default BottomBar;
