import React, { createContext } from "react";
import { createTaskService } from "../task-service";
import { createTaskCategoryService } from "../task-category-service";

export const ServicesContext = createContext();
export const ServicesProvider = (props) => {
  const taskService = createTaskService();
  const taskCategoryService = createTaskCategoryService();
  return (
    <ServicesContext.Provider value={{ taskService, taskCategoryService }}>
        {props.children}
    </ServicesContext.Provider>
  );
};
