// somewhere in your app
import { Text } from "@react-native-material/core";
import {
  Menu,
  MenuOption,
  MenuOptions,
  MenuTrigger,
} from "react-native-popup-menu";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { componentConstants } from "../utils/component-contants";

export const UserMenu = ({ navigation }) => (
  <Menu>
    <MenuTrigger>
      <Icon name={"menu"} size={30} color={"#3BFFFF"}></Icon>
    </MenuTrigger>
    <MenuOptions>
      <MenuOption
        style={{ padding: 10 }}
        onSelect={() => {
          navigation.navigate("UserSettings");
        }}
      >
        <Text>Settings</Text>
      </MenuOption>
      {componentConstants.HAS_USER_SIGN_IN && (
        <MenuOption
          style={{ padding: 10 }}
          onSelect={() => {
            navigation.navigate("UserSignIn");
          }}
        >
          <Text>Sign In</Text>
        </MenuOption>
      )}
      {componentConstants.HAS_USER_PAYMENT && (
        <MenuOption
          style={{ padding: 10 }}
          onSelect={() => {
            navigation.navigate("UserPayment");
          }}
        >
          <Text>Payment</Text>
        </MenuOption>
      )}
    </MenuOptions>
  </Menu>
);
