
import { TaskListModel } from "../../models/task/task-list-model";
import TaskModel from "../../models/task/task-model";

export class TasksUtil extends TaskListModel {

  add(items, value) {
    items.push(value);
    return items;
  }

  update(items, value) {
    return items.map((t) => {
      if (t.id === value.id) {
        return value;
      } else {
        return t;
      }
    });
  }

  undo(items, value) {
    return items.map(item => {
      if(item.id === value.id) {
        item.deleted = false;
      }
      return item;
    });
  }

  softDelete(items, value) {
    let index = -1;
    return items.map((item, i) => {
      if (item.id == value.id) {
        item.deleted = true;
        index = i;
      }
      return item;
    });
  }

  remove(items, value) {
    return items.filter((item) => {
      return item.id !== value.id && item.deleted !== true;
    });
  }

  mapToModel(items) {
    return items.map((item) => {
      return new TaskModel(item);
    });
  }
  filterDeleted(items) {
    return items.filter((item) => {
      return !item.deleted;
    });
  }
}
export const tasksUtil = new TasksUtil()