import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { taskDao } from "models/task/task-dao";
import { TaskListValidator } from "models/task/task-validator";
import { log } from "utils/log";
import { TaskValidator } from "../models/task/task-validator";
// use context get global
import { tasksUtil } from "../services/utils/task.util";
import { componentConstants } from "../components/utils/component-contants";

export const taskSlice = createSlice({
  name: "task",
  initialState: {
    value: [],
  },
  reducers: {
    setTasks: (state, action) => {
      log.debug("taskSlice setTasks", action);
      if (!action?.payload) {
        return;
      }
      state.value = action.payload;
    },
  },
});

export const saveTasksByCategory = createAsyncThunk(
  "task/saveTasksByCategory",
  async (payload, { dispatch }) => {
    const { categoryId, tasks, skipSetStore } = payload;
    await TaskListValidator.validate(tasks);
    await taskDao.setTasksByCategory(categoryId, JSON.stringify(tasks));
    !skipSetStore && dispatch(setTasks(tasks));
  }
);

export const saveRecent = createAsyncThunk(
  "task/saveRecent",
  async (payload) => {
    const { recent, isUpdate } = payload;
    await TaskValidator.validate(recent);
    let rawTasks =
      JSON.parse(await taskDao.getTasksByCategory(componentConstants.RECENT_ACTIONS)) ||
      [];
    let tasks = tasksUtil.mapToModel(rawTasks);
    // if update item, we need remove it first then add it as new item
    if (isUpdate || tasks.some((task) => task.id === recent.id)) {
      tasks = tasks.filter((task) => task.id !== recent.id);
    }
    if (tasks.length >= componentConstants.RECENT_SIZE) {
      tasks = tasks.slice(1, componentConstants.RECENT_SIZE);
    }
    tasks.push(recent);
    await taskDao.setTasksByCategory(
      componentConstants.RECENT_ACTIONS,
      JSON.stringify(tasks)
    );
  }
);

export const removeRecent = createAsyncThunk(
  "task/removeRecent",
  async (payload) => {
    const { recent } = payload;
    await TaskValidator.validate(recent);
    let rawTasks =
      JSON.parse(await taskDao.getTasksByCategory(componentConstants.RECENT_ACTIONS)) ||
      [];
    rawTasks = tasksUtil.filterDeleted(rawTasks);
    await taskDao.setTasksByCategory(
      componentConstants.RECENT_ACTIONS,
      JSON.stringify(rawTasks)
    );
  }
);

