import DaoBase from "../dao-base";

export default class UserSettingsDao extends DaoBase {
  async get() {
    const key = this.getKeyPrefix();
    return await this.getItem(key);
  }

  async set(value) {
    const key = this.getKeyPrefix();
    await this.setItem(key, value);
  }

  getKeyPrefix() {
    return "user-settings";
  }
}

export const userSettingsDao = new UserSettingsDao();
