import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { Text } from "@react-native-material/core";
import * as React from "react";
import { Image, Pressable, StyleSheet, View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";
import { requestUrlS3 } from "../../services/utils";
import { LinearGradient } from "expo-linear-gradient";

export default function BoxLabel({
  style,
  label,
  textStyle,
  onPress,
  icon,
  awsUrl,
  componentContext,
}) {
  const [imageUrl, setImageUrl] = React.useState(null);

  React.useEffect(() => {
    if (awsUrl) {
      requestUrlS3(awsUrl).then((imageUrl) => setImageUrl(imageUrl));
    }
  }, []);

  const element = (
    <Pressable onPress={onPress}>
      {icon && (
        <View
          style={{
            alignItems: "center",
            justifyContent: "center",
            width: "100%",
          }}
        >
          <LinearGradient
            colors={[
              '#70EFD0',
              '#105853',
            ]}
            style={{
              width: 50,
              height: 50,
              borderRadius: 50,
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Icon
              name={icon}
              color={"#E3FFFD"}
              size={30}
              style={{ alignItems: "flex-start" }}
            ></Icon>
          </LinearGradient>
        </View>
      )}
      <View style={{ alignItems: "center", justifyContent: "center" }}>
        <View
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          {imageUrl && (
            <Image style={styles.image} source={{ uri: imageUrl }} />
          )}
          <Text style={textStyle ? textStyle : styles.text}>{label || ""}</Text>
        </View>
      </View>
    </Pressable>
  );

  if (componentContext === "linear-gradient") {
    return (
      <LinearGradient
        colors={["#105853", "#388E88"]}
        start={{x: 0, y: 1}}
        end={{x: 1, y: 0}}
        style={style ? style : [styles.box, styles.boxLinear]}
      >
        {element}
      </LinearGradient>
    );
  }
  return <View style={style ? style : styles.box}>{element}</View>
}
const styles = StyleSheet.create({
  box: {
    width: 150,
    height: "45%",
    borderRadius: 10,
    margin: 3,
    padding: 10,
    backgroundColor: layoutConstants.bgColorSecondary,
  },
  boxLinear: {
    borderWidth: 1,
    borderColor: '#36B9B0'
  },
  text: {
    color: '#5FD3CA',
  },
  image: {
    width: 60,
    height: 60,
    borderRadius: 10,
  },
});
