import { Text } from "@react-native-material/core";
import * as React from "react";
import { Pressable, StyleSheet, View } from "react-native";

export default function TaskList(props) {
  const { tasks, onPress, textStyle } = props;
  return (
    <View style={{ flex: 1 }}>
      {tasks &&
        tasks.map((task, index) => {
          const date = task.date ? new Date(task.date) : "";
          const time = task.time ? new Date(task.time) : "";
          let timeDisplay;
          if (date || time) {
            if (!date) {
              timeDisplay = time;
            }
            if (!time) {
              timeDisplay = date;
            }
            if (date && time) {
              timeDisplay = date;
              timeDisplay.setHours(time.getHours());
              timeDisplay.setMinutes(time.getMinutes());
            }
          }
          return (
            <Pressable
              onPress={() => {
                onPress(task);
              }}
              key={task.id + index}
            >
              <View style={[styles.listItem]}>
                <View
                  style={{
                    width: "100%",
                    paddingLeft: 10,
                    margin: 5,
                    display: "flex",
                  }}
                >
                  {!!task.title && (
                    <Text style={textStyle ? textStyle : styles.description}>{task.title}</Text>
                  )}
                  {!!task.description && (
                    <Text style={textStyle ? textStyle : styles.description}>{task.description}</Text>
                  )}
                  {!!timeDisplay && (
                    <Text style={styles.time}>
                      {timeDisplay && timeDisplay.toLocaleDateString("en-US")}
                    </Text>
                  )}
                </View>
              </View>
            </Pressable>
          );
        })}
    </View>
  );
}

const styles = StyleSheet.create({
  listItem: {
    display: "flex",
    flexDirection: "row",
    borderBottomColor: "rgba(53, 181, 172, 0.3)",
    borderBottomWidth: 2,
    paddingLeft: 5,
    alignItems: "center",
    minHeight: 50,
  },
  description: {
    color: 'black',
    fontSize: 12,
  },
  time: {
    color: "green",
    fontSize: 12,
  },
});
