import { Button } from "@react-native-material/core";
import { StyleSheet, View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";
import TaskList from "../task/task-list";

export default function RecentTasks({ tasks, navigation }) {
  // click recent action, navigate to action detail
  const onPressAction = (task) => {
    navigation.navigate("ActionDetail", {
      task: task ? task : null,
      categoryId: task?.categoryId,
      back: { component: "Home" },
    });
  };

  // click see more, navigate to actions
  const onPressSeeMore = () => {
    navigation.navigate("Actions");
  };

  return (
    tasks?.length > 0 && (
      <View style={styles.body}>
        <TaskList
          textStyle={{ color: "white" }}
          tasks={tasks.reverse()}
          onPress={onPressAction}
        />
        <Button
          title="See more actions"
          variant="text"
          onPress={onPressSeeMore}
          color={"white"}
          titleStyle={{ fontSize: 12 }}
        />
      </View>
    )
  );
}

const styles = StyleSheet.create({
  body: {
    borderTopStartRadius: 70,
    borderTopRightRadius: 70,
    padding: 5,
    backgroundColor: layoutConstants.bgColorThird,
    borderTopWidth: 1,
    borderRightWidth: 1,
    borderLeftWidth: 1,
    borderColor: "rgba(159, 255, 249, 0.3)",
  },
});
