import { componentConstants } from "../../components/utils/component-contants";
import { constants } from "../../themes/layout-contants";
import TaskModel from "./task-model";

export class TaskListModel {
  items = [];

  get(includeDeleted = false) {
    if (!includeDeleted) {
      return this.items.filter((task) => {
        return !task.deleted;
      });
    }
    return this.items;
  }

  add(task) {
    this.items.push(task);
    return this.get(false);
  }

  softDelete(value) {
    let index = -1;
    this.items = this.items.map((task, i) => {
      if (task.id == value.id) {
        task.deleted = true;
        index = i;
      }
      return task;
    });
    return index;
  }

  set(rawTasks) {
    if(rawTasks) {
      this.items = rawTasks.map((taskJson) => {
        const task = new TaskModel(taskJson);
        if (!task.categoryId) {
          task.categoryId = componentConstants.MY_ACTIONS;
        }
        return task;
      });
    }
  }

  remove(value) {
    const currentLength = this.items.length;
    this.items = this.items.filter((task) => {
      return task.id !== value.id && task.deleted !== true;
    });
    return currentLength !== this.items.length;
  }

  undo(value) {
    this.items = this.items.map(task => {
      if(task.id === value.id) {
        task.deleted = false;
      }
      return task;
    });
  }

  update(task) {
    this.items = this.items.map((t) => {
      if (t.id === task.id) {
        return task;
      } else {
        return t;
      }
    });
    return this.get(false);
  }
}
