import { Text } from "@react-native-material/core";
import dayjs from "dayjs";
import { View } from "react-native";

export default function TodayCalendar({textStyle}) {
  const date = dayjs();
  const month = date.format("MMM");
  const day = date.format("DD");
  const year = date.format("YYYY");

  return (
    <View style={{ display: "flex", flexDirection: "row", flex: 1 }}>
      <Text style={textStyle ?? textStyle} variant="h2" color="#3BFFFF">
        {day}
      </Text>
      <View style={{ margin: 5 }}>
        <Text style={textStyle ?? textStyle} variant="h5" color="#3BFFFF">
          {month}
        </Text>
        <Text style={textStyle ?? textStyle} variant="h5" color="#3BFFFF">
          {year}
        </Text>
      </View>
    </View>
  );
}
