import { userSettingsDao } from "../models/user/user-settings-dao";

export default class UserSettingsService {

  userSettingsDefault = {
    notificationsTimes: '2',
    notificationsStartTime: "07:37",
    notificationsEndTime: "07:39",
  };

  async get() {
    // return JSON.stringify(this.userSettingsDefault);
    const settings = await userSettingsDao.get();
    if(!settings) {
      return JSON.stringify(this.userSettingsDefault);
    }
    return settings;
  }

  async set(value) {
    if(typeof value !== 'string') {
        value = JSON.stringify(value);
    }
    await userSettingsDao.set(value);
  }
}

let service;
const createUserSettingsService = () => {
  if (service) {
    return service;
  }
  service = new UserSettingsService();
  return service;
};

export { createUserSettingsService };

