import { ScrollView, StyleSheet, View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";
import { componentConstants } from "./component-contants";
import { useOrientation } from "./use-orientation";

export function ViewLandscape({ children, noScroll }) {
  const direction = useOrientation();
  const isLandscape = direction === componentConstants.LANDSCAPE;
  const containerStyle = [styles.container];
  isLandscape && containerStyle.push({ width: "70%", margin: "auto" });
  return (
    <View
      style={{
        width: "100%",
        height: "100%",
        display: "flex",
      }}
    >
      {!!noScroll ? (
        <View style={containerStyle}>{children}</View>
      ) : (
        <ScrollView contentContainerStyle={{ minHeight: "95%", marginBottom: '10%' }}>
          <View style={containerStyle}>{children}</View>
        </ScrollView>
      )}
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    color: layoutConstants.colorPrimary,
  },
});
