import { Provider } from "@react-native-material/core";
import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { Amplify } from "aws-amplify";
import * as React from "react";
import { MenuProvider } from "react-native-popup-menu";
import { Provider as ReduxProvider } from "react-redux";
import HomeScreen from "screens/home-screen";
import QuoteDetailScreen from "screens/quote-detail-screen";
import TaskScreen from "screens/task-screen";
import awsmobile from "./app/aws-configs";
import CategoryDetailScreen from "./app/screens/category-detail-screen";
import TaskDetailScreen from "./app/screens/task-detail-screen";
import { UserPaymentScreen } from "./app/screens/user-payment-screen";
import { UserSettingsScreen } from "./app/screens/user-settings-screen";
import { UserSignInScreen } from "./app/screens/user-sign-in-screen";
import { ServicesProvider } from "./app/services/providers/services-provider";
import store from "./app/store";
import { layoutConstants } from "./app/themes/layout-contants";

Amplify.configure(awsmobile);

const Stack = createNativeStackNavigator();

export default function App() {
  const config = {
    screens: {
      QuoteDetail: "quote",
      Actions: "actions",
      Home: "home",
    },
  };

  const linking = {
    prefixes: ["https://elearning01.github.io"],
    config,
  };

  return (
    <ReduxProvider store={store}>
      <Provider>
        <ServicesProvider>
          <MenuProvider>
            <NavigationContainer linking={linking}>
              <Stack.Navigator>
                <Stack.Screen
                  name="Home"
                  component={HomeScreen}
                  options={{
                    headerShown: false,
                  }}
                />
                <Stack.Screen
                  name="UserSettings"
                  component={UserSettingsScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Settings",
                    headerTitleAlign: "center",
                  }}
                />
                <Stack.Screen
                  name="UserSignIn"
                  component={UserSignInScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Sign In",
                    headerTitleAlign: "center",
                  }}
                />
                <Stack.Screen
                  name="UserPayment"
                  component={UserPaymentScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Payment",
                    headerTitleAlign: "center",
                  }}
                />
                <Stack.Screen
                  name="Actions"
                  component={TaskScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Actions",
                    headerTitleAlign: "center",
                  }}
                />
                <Stack.Screen
                  name="ActionDetail"
                  component={TaskDetailScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Action Detail",
                    headerTitleAlign: "center",
                  }}
                />
                <Stack.Screen
                  name="ActionCategoryDetail"
                  component={CategoryDetailScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Action Category",
                    headerTitleAlign: "center",
                  }}
                />
                <Stack.Screen
                  name="QuoteDetail"
                  component={QuoteDetailScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    title: "Quote",
                    headerTitleAlign: "center",
                  }}
                />
              </Stack.Navigator>
            </NavigationContainer>
          </MenuProvider>
        </ServicesProvider>
      </Provider>
    </ReduxProvider>
  );
}
