import * as Device from "expo-device";
import * as Notifications from "expo-notifications";
import { useEffect, useRef, useState } from "react";
import { Platform } from "react-native";
import { log } from "utils/log";
import { createUserSettingsService } from "../../services/user-settings-service";

Notifications.setNotificationHandler({
  handleNotification: async () => ({
    shouldShowAlert: true,
    shouldPlaySound: true,
    shouldSetBadge: true,
  }),
});

// Can use this function below OR use Expo's Push Notification Tool from: https://expo.dev/notifications
async function sendPushNotification(expoPushToken) {
  const message = {
    to: expoPushToken,
    sound: "default",
    title: "Original Title",
    body: "And here is the body!",
    data: { someData: "goes here" },
  };

  await fetch("https://exp.host/--/api/v2/push/send", {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Accept-encoding": "gzip, deflate",
      "Content-Type": "application/json",
    },
    body: JSON.stringify(message),
  });
}

async function registerForPushNotificationsAsync() {
  log.debug("registerForPushNotificationsAsync");
  let token;
  if (Device.isDevice) {
    const { status: existingStatus } =
      await Notifications.getPermissionsAsync();
    let finalStatus = existingStatus;
    if (existingStatus !== "granted") {
      const { status } = await Notifications.requestPermissionsAsync();
      finalStatus = status;
    }
    if (finalStatus !== "granted") {
      return;
    }
    token = (await Notifications.getExpoPushTokenAsync()).data;
  } else {
    alert("Must use physical device for Push Notifications");
  }

  if (Platform.OS === "android") {
    Notifications.setNotificationChannelAsync("default", {
      name: "default",
      importance: Notifications.AndroidImportance.MAX,
      vibrationPattern: [0, 250, 250, 250],
      lightColor: "#FF231F7C",
    });
  }

  await setupNotificationsTime();
  setupNotifications();
  return token;
}

export let notificationTime = [];
export function useNotifications(navigation) {
  const [expoPushToken, setExpoPushToken] = useState("");
  const [notification, setNotification] = useState(false);
  const notificationListener = useRef();
  const responseListener = useRef();

  useEffect(() => {
    registerForPushNotificationsAsync().then((token) =>
      setExpoPushToken(token)
    );

    notificationListener.current =
      Notifications.addNotificationReceivedListener((notification) => {
        setNotification(notification);
      });

    responseListener.current =
      Notifications.addNotificationResponseReceivedListener((response) => {
        log.debug("addNotificationResponseReceivedListener");
        // navigation.navigate(constants.QUOTE_SCREEN, {quote: response.notification.request.content.title});
      });

    return () => {
      Notifications.removeNotificationSubscription(
        notificationListener.current
      );
      Notifications.removeNotificationSubscription(responseListener.current);
    };
  }, []);
}

export const setupNotificationsTime = async () => {
  const userSettingsService = createUserSettingsService();
  const settings = JSON.parse(await userSettingsService.get());
  const startTime = settings.notificationsStartTime.split(":");
  const endTime = settings.notificationsEndTime.split(":");
  const times = parseInt(settings.notificationsTimes);

  let dateStart = new Date();
  dateStart.setHours(startTime[0]);
  dateStart.setMinutes(startTime[1]);

  const dateEnd = new Date();
  dateEnd.setHours(endTime[0]);
  dateEnd.setMinutes(endTime[1]);

  const diff = Math.abs(dateEnd - dateStart);
  const step = diff / times;
  let notifications = [];
  for (let i = 0; i < times; i++) {
    const notiTime = new Date(dateStart.getTime() + i * step);
    notifications.push(notiTime);
  }
  notificationTime = notifications;
};

// Default messages
let notificationMessages = [
  "Good things come your way when you serve the people around you",
  "With persistent perseverance, you can break through any barrier.",
  "Wasting and losing time is equivalent to wasting and losing your life",
  "Money is made only from time well converted.",
  "People who do not waste their life truly understand the importance of time.",
];
export function setupNotifications(messages, time) {
  log.debug("setupNotifications start");
  if (messages?.length > 0) {
    notificationMessages = messages;
  }
  if (time?.length > 0) {
    notificationTime = time;
  }

  Notifications.cancelAllScheduledNotificationsAsync();
  for (let i = 0; i < notificationTime.length; i++) {
    Notifications.scheduleNotificationAsync({
      content: {
        title: "Motify Notification",
        body: notificationMessages[i],
      },
      trigger: {
        // type: "daily",
        hour: notificationTime[i].getHours(),
        minute: notificationTime[i].getMinutes(),
        repeats: true,
      },
    });
    log.debug("notificationMessages: ", notificationMessages[i]);
    log.debug("notificationTime: ", notificationTime[i]);
  }
}
